
import { getPageListData } from '@/service/main/system/system'
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import SingleTopic from '@/components/page-topic/src/cpns/single-topic.vue'

export default defineComponent({
  components: {
    SingleTopic
  },
  setup() {
    const router = useRouter()
    const searchValue = ref('')
    let searchTopicList = ref()
    const loading = ref(false)
    const finished = ref(false)

    const handleBackClick = () => {
      router.back()
    }

    const handleSearchClick = async () => {
      loading.value = true
      const res = await getPageListData('/topic/search', {
        offset: searchTopicList.value?.length ?? 0,
        limit: 10,
        intro: searchValue.value
      })
      console.log(res)
      searchTopicList.value = res.data.list
      finished.value = false
    }

    const handleCancelSearch = () => {
      searchValue.value = ''
      searchTopicList.value = []
    }

    return {
      searchValue,
      loading,
      finished,
      searchTopicList,
      handleBackClick,
      handleSearchClick,
      handleCancelSearch
    }
  }
})
